import { IoAlertCircleOutline } from "react-icons/io5";
import Tooltip from "@mui/material/Tooltip";
import React, { SelectHTMLAttributes } from "react";
import classNames from "classnames";
import { GroundType } from "utils/types";

const WarningIcon = React.forwardRef(function MyComponent(props: any, ref: any) {
  return (
    <div {...props} ref={ref}>
      <IoAlertCircleOutline className="text-red-FF0000" size={20}></IoAlertCircleOutline>
    </div>
  );
});

export interface SelectProps extends SelectHTMLAttributes<HTMLSelectElement> {
  className?: string;
  options: { label: string; value: string | number }[];
  error?: string | boolean;
  ground: GroundType;
}

const Select: React.FC<SelectProps> = ({ className = "", options, error, ground, ...props }) => {
  let mainClasses = `input${ground}TextColor input${ground}HoverBackground input${ground}FocusedTextColor input${ground}FocusedBackground input${ground}FocusedBorderColor input${ground}Background`;
  return (
    <div className={`${className} relative flex items-center`}>
      <select
        className={classNames("px-3 h-full border-2 border-solid w-full rounded outline-none", {
          "border-red-FF0000": error,
          [mainClasses]: !error,
        })}
        {...props}
        spellCheck={false}
      >
        {options.map((x, i) => (
          <option data-testid={`select-option-${x.value}`} key={"opt-" + i} value={x.value}>
            {x.label}
          </option>
        ))}
      </select>
      {error ? (
        <Tooltip title={error} placement="top">
          <WarningIcon className="absolute right-2 my-auto w-auto"></WarningIcon>
        </Tooltip>
      ) : null}
    </div>
  );
};

export default Select;
